module.exports = "<div id=app> <mt-header title=图书馆订阅系统 style=\"font-size: 20px\"> </mt-header> <div class=login v-if=!isLogin> <mt-field label=学号 placeholder=请输入学号 type=text :value.sync=stuId></mt-field> <mt-field label=密码 placeholder=请输入密码/默认为123456 type=password :value.sync=password></mt-field> <mt-button class=login-button size=large type=primary @click=login(this.value)> 登陆 </mt-button> </div> <mt-tab-container :active.sync=selected> <mt-tab-container-item id=借阅> <mt-cell v-for=\"ele in lend\" :title=\"ele.title | lendTitle\" :label=\"ele.lend | lendTime\"> <span v-if=\"ele.remain > 7\" style=\"color: #4caf50\"> {{ele.remain | remain}} </span> <span v-if=\"ele.remain > 0 && ele.remain <= 7\" style=\"color: #FF5722\"> {{ele.remain | remain}} </span> <span v-if=\"ele.remain < 0\" style=\"color: red\"> 超期：{{ele.remain | remain}} </span> </mt-cell> </mt-tab-container-item> <mt-tab-container-item id=订阅> <div class=book-cell> <mt-cell v-for=\"ele in books\" :title=\"ele.title | title\" :label=\"ele.location | location\"> <span style=\"color: green\" v-if=ele.canBorrowNum> 可借: {{ele.canBorrowNum}} </span> <span v-else=!ele.canBorrowNum> 可借: {{ele.canBorrowNum}} </span> <span class=\"mint-field-state is-error\" v-if=showDel @click=delBook($index)> <i class=\"mintui mintui-field-error\"></i> </span> </mt-cell> </div> <div class=book-add v-if=showAdd> <mt-field label=书籍号 placeholder=\"eg: 0000805778\" :state=addBookState :value.sync=addBook> </mt-field> </div> <div class=set-mail v-if=showMail> <mt-field label=邮箱 placeholder=用于提醒你有书可借 type=text :state=emailState :value.sync=user.email> </mt-field> </div> <div class=operate v-if=showOperate> <mt-button type=primary size=normal v-if=!showSave @click=addSub>增加订阅</mt-button> <mt-button type=danger size=normal v-if=!showSave @click=delSub>删除订阅</mt-button> <mt-button style=width:91% type=primary plain v-if=!showSave @click=addMail> 设置邮箱 </mt-button> <mt-button style=\"background-color: #4CAF50; width:92%\" type=primary v-if=showSave @click=save> 保存 </mt-button> </div> </mt-tab-container-item> <mt-tab-container-item id=搜索> <mt-search :value.sync=search.title :result.sync=search.result cancel-text=取消 placeholder=搜索 @keyup.enter=searchBook> <div v-infinite-scroll=loadMore() infinite-scroll-distance=10> <mt-cell v-for=\"ele in search.result\" :title=ele.title :label=ele.author> </mt-cell> <div class=search-spinner> <mt-spinner type=snake v-if=search.loading></mt-spinner> </div> </div> </mt-search> </mt-tab-container-item> </mt-tab-container> <mt-tabbar :selected.sync=selected fixed v-if=isLogin> <mt-tab-item id=借阅> <i class=\"iconfont icon-book\"></i> 借阅 </mt-tab-item> <mt-tab-item id=订阅> <i class=\"iconfont icon-news\"></i> 订阅 </mt-tab-item> <mt-tab-item id=搜索> <i class=\"iconfont icon-search\"></i> 搜索 </mt-tab-item> </mt-tabbar> <div class=fix-fixed></div> </div>";


